@import '../../node_modules/normalize.css/normalize';
@import 'colors';
@import 'fonts';
@import 'breakpoints';
@import 'vars';

ul {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Helvetica Neue';
  min-height: 100vh;
  color: $text;
}
.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.assortment {
  padding-top: 35px;
  padding-left: 130px;
  padding-right: 130px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: $large-max) {
    padding-left: 10%;
    padding-right: 10%;
  }

  &__title {
    line-height: 1.2;
    font-family: 'Exo 2';
    font-size: 36px;
    margin-top: 0;

    @media (max-width: $extra-small-max) {
      text-align: center;
    }
  }

  &__brands-list {
    list-style: none;
    display: flex;
    width: 600px;
    justify-content: space-between;
    margin-bottom: 20px;
    font-size: 18px;
    font-family: 'Helvetica Neue';

    @media (max-width: $small-max) {
      flex-wrap: wrap;
      justify-content: space-around;
      width: 75%;
    }

    @media (max-width: $extra-small-max) {
      flex-wrap: wrap;
      justify-content: space-around;
      width: 100%;
    }
  }

  &__brands-item-button {
    width: 140px;
    height: 50px;
    border: none;
    background: $brands-list-item-bg;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    @media (max-width: $small-max) {
      margin: 10px;
    }

    &.active-tab {
      background: $bonfesto-logo-bg;
    }
  }

  & .all-brands {
    line-height: 1.2;
    color: $text-brands-tab;

    & span {
      border-bottom: dotted 1px $text-brands-tab;
    }
  }

  &__products-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;

    @media (max-width: $small-max) {
      justify-content: center;
    }
  }
}

.product {
  width: calc((100% / 3) - (2 * #{$product-item-margin} / 3));
  max-width: 350px;
  margin-right: $product-item-margin;
  margin-bottom: 25px;

  @media (min-width: $large-min) {
    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  @media (max-width: $medium-max) and (min-width: $medium-min) {
    width: calc((100% / 2) - (#{$product-item-margin} / 2));
    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  @media (max-width: $small-max) {
    width: 100%;
    margin-bottom: 40px;
    margin-right: 0;
  }

  & > img {
    width: 100%;
  }

  &__category {
    font-size: 15px;
    margin-bottom: 5px;
  }

  &__name {
    width: fit-content;
    margin-top: 0;
    font-family: 'Exo 2';
  }

  &__title {
    color: $text-product-title;
    width: fit-content;
    text-decoration-style: dotted;
    font-size: 24px;
    line-height: 1.4;

    &-flavour {
      display: block;
      font-size: 20px;
    }

    &:hover {
      color: $text-product-title-hover;
    }
  }

  &__brand-logo {
    background: $bonfesto-logo-bg;
    width: 70px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;

    & > img {
      width: 70%;
    }
  }

  &__description {
    font-size: 15px;
    line-height: 1.2;

    @media (max-width: $small-max) {
      font-size: 17px;
    }
  }
}

_:-ms-fullscreen,
:root .assortment__products-list {
  max-width: 1200px;
  padding: 0 130px;
  box-sizing: border-box;
}

@media (max-width: $medium-max) and (min-width: $medium-min) and (-ms-high-contrast: none),
  (-ms-high-contrast: active) {
  .assortment__products-list {
    max-width: 991px !important;
  }
}

@media (max-width: $small-max) and (-ms-high-contrast: none),
  (-ms-high-contrast: active) {
  .assortment__products-list {
    max-width: 767px !important;
  }
}
