@font-face {
  font-family: 'Exo 2';
  src: url("../assets/fonts/Exo2-Medium.woff") format("woff"), url("../assets/fonts/Exo2-Medium.woff2") format("woff2");
  font-weight: normal; }

@font-face {
  font-family: 'Helvetica Neue';
  src: url("../assets/fonts/HelveticaNeueCyr-Light.woff") format("woff"), url("../assets/fonts/HelveticaNeueCyr-Light.woff2") format("woff2");
  font-weight: normal; }

ul {
  margin: 0;
  padding: 0; }

body {
  font-family: 'Helvetica Neue';
  min-height: 100vh;
  color: #1d1813; }

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto; }

.assortment {
  padding-top: 35px;
  padding-left: 130px;
  padding-right: 130px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (max-width: 1199px) {
    .assortment {
      padding-left: 10%;
      padding-right: 10%; } }
  .assortment__title {
    line-height: 1.2;
    font-family: 'Exo 2';
    font-size: 36px;
    margin-top: 0; }
    @media (max-width: 575px) {
      .assortment__title {
        text-align: center; } }
  .assortment__brands-list {
    list-style: none;
    display: flex;
    width: 600px;
    justify-content: space-between;
    margin-bottom: 20px;
    font-size: 18px;
    font-family: 'Helvetica Neue'; }
    @media (max-width: 767px) {
      .assortment__brands-list {
        flex-wrap: wrap;
        justify-content: space-around;
        width: 75%; } }
    @media (max-width: 575px) {
      .assortment__brands-list {
        flex-wrap: wrap;
        justify-content: space-around;
        width: 100%; } }
  .assortment__brands-item-button {
    width: 140px;
    height: 50px;
    border: none;
    background: #f5f3e8;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer; }
    @media (max-width: 767px) {
      .assortment__brands-item-button {
        margin: 10px; } }
    .assortment__brands-item-button.active-tab {
      background: #8fb52c; }
  .assortment .all-brands {
    line-height: 1.2;
    color: #77716a; }
    .assortment .all-brands span {
      border-bottom: dotted 1px #77716a; }
  .assortment__products-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap; }
    @media (max-width: 767px) {
      .assortment__products-list {
        justify-content: center; } }

.product {
  width: calc((100% / 3) - (2 * 20px / 3));
  max-width: 350px;
  margin-right: 20px;
  margin-bottom: 25px; }
  @media (min-width: 992px) {
    .product:nth-child(3n) {
      margin-right: 0; } }
  @media (max-width: 991px) and (min-width: 768px) {
    .product {
      width: calc((100% / 2) - (20px / 2)); }
      .product:nth-child(2n) {
        margin-right: 0; } }
  @media (max-width: 767px) {
    .product {
      width: 100%;
      margin-bottom: 40px;
      margin-right: 0; } }
  .product > img {
    width: 100%; }
  .product__category {
    font-size: 15px;
    margin-bottom: 5px; }
  .product__name {
    width: fit-content;
    margin-top: 0;
    font-family: 'Exo 2'; }
  .product__title {
    color: #3c2f20;
    width: fit-content;
    text-decoration-style: dotted;
    font-size: 24px;
    line-height: 1.4; }
    .product__title-flavour {
      display: block;
      font-size: 20px; }
    .product__title:hover {
      color: #5b84f0; }
  .product__brand-logo {
    background: #8fb52c;
    width: 70px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center; }
    .product__brand-logo > img {
      width: 70%; }
  .product__description {
    font-size: 15px;
    line-height: 1.2; }
    @media (max-width: 767px) {
      .product__description {
        font-size: 17px; } }

_:-ms-fullscreen,
:root .assortment__products-list {
  max-width: 1200px;
  padding: 0 130px;
  box-sizing: border-box; }

@media (max-width: 991px) and (min-width: 768px) and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .assortment__products-list {
    max-width: 991px !important; } }

@media (max-width: 767px) and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .assortment__products-list {
    max-width: 767px !important; } }
